@import url(https://fonts.googleapis.com/css2?family=Lato:ital,wght@0,300;0,400;0,700;1,300;1,400;1,700&display=swap);
.container{width:100%;max-width:500px;margin:0 auto}.container .row{width:90%;margin:0 auto;height:100%;display:flex;justify-content:center}.container .row [class*='col-']{min-height:1px}.container .row .col-1{width:8.33333%}.container .row .col-2{width:16.66667%}.container .row .col-3{width:25%}.container .row .col-4{width:33.33333%}.container .row .col-5{width:41.66667%}.container .row .col-6{width:50%}.container .row .col-7{width:58.33333%}.container .row .col-8{width:66.66667%}.container .row .col-9{width:75%}.container .row .col-10{width:83.33333%}.container .row .col-11{width:91.66667%}.container .row .col-12{width:100%}*{box-sizing:border-box;margin:0;padding:0}body{font-size:16px;font-family:"Lato",sans-serif;line-height:24px;font-weight:400;background-color:skyblue}a{text-decoration:none}

main.container{min-height:calc(100vh - 100px - 50px)}

header.container{height:100px}header.container .row{align-items:center}header.container .row .header__title{text-align:center}


main.container .row__tdList .tdList{list-style:none}


footer.container{height:50px}footer.container .row{align-items:center}footer.container .row .footer__text{text-align:center}footer.container .row button{position:absolute;right:0;bottom:0}

